          <div class="study-tips-content"><span class="study-tips-icon">📒</span>每次对话后花几分钟总结策略的要点，有助于知识的长期巩固。</div>
        </div>
      </div>
    </div>
    
    <!-- 设置模态框 -->
    <div v-if="showSettingsModal" class="modal-overlay" @click="closeSettings">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>设置对话模式</h3>
          <button class="modal-close" @click="closeSettings">×</button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label for="training">对话类型：</label>
            <select id="training" v-model="currentTraining" class="form-select">
              <option value="引导性对话">引导性对话</option>
              <option value="非引导性对话">非引导性对话</option>
            </select>
          </div>
          <div class="form-group">
            <label for="title">冲突场景：</label>
            <select id="title" v-model="currentTitle" class="form-select">
              <option value="零花钱的使用">零花钱的使用</option>
              <option value="家务分配不均">家务分配不均</option>
              <option value="财务支出争议">财务支出争议</option>
              <option value="子女教育争议">子女教育争议</option>
            </select>
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn-cancel" @click="closeSettings">取消</button>
          <button class="btn-save" @click="saveSettings">保存</button>
        </div>
      </div>
