</template>

<script>
import axios from 'axios';
export default {
  name: 'HomePage',
  data() {
    return {
      input: '',
      messages: [
        {
          role: 'bot',
          text: '你好！我是你的Conflict Simulation助手。我可以帮助你模拟和分析各种冲突场景，并提供个性化的指导。'
        }
      ],
      loading: false,
      scoreFeedback: null,
      teachFeedback: [],
      scoreFeedbackLoading: false,
      teachFeedbackLoading: false,
      showSettingsModal: false,
      currentTraining: '引导性对话',
      currentTitle: '零花钱的使用',
      conversation_id: "",
      score_id:"",
      teach_id:"",
      quit:0,
    }
  },
  computed: {
    lastTime() {
      return '17:25';
    }
  },
  methods: {
    async sendMessage() {
      const text = this.input && this.input.trim();
      if (!text || this.loading) return;
      const title = this.currentTitle; // 使用动态title
      console.log(this.currentTitle);
      console.log(this.currentTraining);
      this.messages.push({ role: 'user', text });
      this.input = '';
      this.loading = true;
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      // 先插入一个空的机器人气泡
      const botMsg = { role: 'bot', text: '' };
      this.messages.push(botMsg);
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      let botResponse = '';
      try {
        const response = await fetch('http://dify.ai-role.cn/v1/chat-messages', {
          method: 'POST',
          headers: {
            'Authorization': 'Bearer app-1FfMKOZZ12LF8HXlpXdTopPG',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            query: text,
            inputs: {
              training: this.currentTraining, // 使用动态training
              title: title,
              quit:this.quit
            },
            response_mode: "streaming",
            user: "abc-123",
            conversation_id:this.conversation_id
          })
        });
        if (!response.body || !window.ReadableStream) {
          // fallback: 不是流式响应
          const data = await response.json();
          botMsg.text = data.answer || data.response || '（大模型无回复）';
          botResponse = botMsg.text;
          
          // 获取conversation_id
          if (data.conversation_id) {
            this.conversation_id = data.conversation_id;
          }
          
          this.loading = false;
          // 机器人回复后，拉取反馈
          this.get_score_feedback(title, text, botResponse);
          this.get_teach_feedback(title, text, botResponse);
          return;
        }
        const reader = response.body.getReader();
        // console.log(response);
        const decoder = new TextDecoder('utf-8');
        let done = false;
        let buffer = '';
        // let conversationIdReceived = false;
        
        while (!done) {
          const { value, done: doneReading } = await reader.read();
          done = doneReading;
          if (value) {
            buffer += decoder.decode(value, { stream: true });
            // 以换行分割流式片段
            let lines = buffer.split('\n');
            buffer = lines.pop();
            for (const line of lines) {
              if (!line.trim()) continue;
              if (line.startsWith('event: ping')) continue; // 跳过心跳包
              // 兼容 SSE 格式: data: xxx
              let content = line;
              if (line.startsWith('data:')) content = line.replace(/^data:\s*/, '');
              try {
                const json = JSON.parse(content);
                if (json.answer) {
                  botMsg.text += json.answer;
                } else if (json.response) {
                  botMsg.text += json.response;
                } else if (typeof json === 'string') {
                  botMsg.text += json;
                }
                // console.log(json);
                this.conversation_id = json.conversation_id;
                // 获取conversation_id（只在第一次收到时设置）
                // if (!conversationIdReceived && json.conversation_id) {
                //   this.conversation_id = json.conversation_id;
                //   conversationIdReceived = true;
                // }
              } catch {
                botMsg.text += content;
              }
              this.$forceUpdate();
              this.$nextTick(() => {
                const chatHistory = this.$el.querySelector('.chat-history');
                if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
              });
            }
          }
        }
        // 处理最后一段
        if (buffer && buffer.trim()) {
          try {
            const json = JSON.parse(buffer);
            if (json.answer) {
              botMsg.text += json.answer;
            } else if (json.response) {
              botMsg.text += json.response;
            } else if (typeof json === 'string') {
              botMsg.text += json;
            }
          } catch {
            botMsg.text += buffer;
          }
          this.$forceUpdate();
        }
        botResponse = botMsg.text;
      } catch (e) {
        console.error('API请求错误:', e);
        botMsg.text = '机器人服务暂时不可用，请稍后再试。';
        botResponse = botMsg.text;
      } finally {
        this.loading = false;
        // 机器人回复后，拉取反馈
        console.log(botResponse);
        if(this.currentTraining === '引导性对话'&& botResponse!="因您连续使用暴力沟通，请您冷静之后再进行回复。Tips：推荐使用NVC策略进行回复。"){
          this.get_score_feedback(title, text, botResponse);
          this.get_teach_feedback(title, text, botResponse);
        }
       }
    },
    async get_score_feedback(title, user_response, bot_response) {
      this.scoreFeedbackLoading = true; // 新增
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'score feedback',
          inputs: {
            title:title,
            user_response:user_response,
            bot_response:bot_response
          },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id:this.score_id
        }, {
          headers: {
            'Authorization': 'Bearer app-XPpxMLpT4EmwJ2VwXwvmOBPt',
            'Content-Type': 'application/json'
          }
        });
        // console.log(res);
        console.log(res.data.answer)
        this.scoreFeedback = res.data.answer;
        this.score_id=res.data.conversation_id;
      } catch (e) {
        this.scoreFeedback = '评分反馈获取失败';
      } finally {
        this.scoreFeedbackLoading = false; // 新增
      }
    },
    async get_teach_feedback(title, user_response, bot_response) {
      this.teachFeedbackLoading = true; // 新增
      // console.log(this.teach_id)
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'teach feedback',
          inputs: {
            title: title,
            user_response: user_response,
            bot_response: bot_response
          },
          response_mode: "blocking",
          user: "abc-123",
          conversation_id:this.teach_id
        }, {
          headers: {
            'Authorization': 'Bearer app-YhRXu8JFuHs9deDyR8AJwYrC',
            'Content-Type': 'application/json'
          }
        });
        // 假设返回字符串 answer
        const str = res.data.answer || '';
        console.log('教学反馈原始数据:', res.data.answer);
        this.teach_id=res.data.conversation_id;
        // 分割字符串为4个卡片
        const cardDefs = [
          { title: 'NVC', icon: '💡', colorClass: 'teach-yellow' },
          { title: 'Proposal', icon: '⭐', colorClass: 'teach-green' },
          { title: 'Concession', icon: '📈', colorClass: 'teach-blue' },
          { title: 'Promise', icon: '🎯', colorClass: 'teach-purple' }
        ];
        
        // 尝试多种匹配方式
        let result = [];
        
        // 方式1: 使用正则表达式匹配
        const regex = /(NVC|Proposal|Concession|Promise)：(.*?)(?=(NVC|Proposal|Concession|Promise)：|$)/gs;
        let match;
        while ((match = regex.exec(str)) !== null) {
          const idx = cardDefs.findIndex(c => c.title === match[1]);
          if (idx !== -1) {
            result[idx] = {
              ...cardDefs[idx],
              content: match[2].trim()
            };
          }
        }
        
        // 方式2: 如果正则匹配失败，尝试按行分割
        if (result.length === 0) {
          const lines = str.split('\n').filter(line => line.trim());
          lines.forEach(line => {
            const colonIndex = line.indexOf('：');
            if (colonIndex > 0) {
              const title = line.substring(0, colonIndex).trim();
              const content = line.substring(colonIndex + 1).trim();
              const idx = cardDefs.findIndex(c => c.title === title);
              if (idx !== -1) {
                result[idx] = {
                  ...cardDefs[idx],
                  content: content
                };
              }
            }
          });
        }
        
        console.log('解析后的教学反馈:', result);
        
        // 保证顺序和完整性
        this.teachFeedback = cardDefs.map((def, i) => result[i] || { ...def, content: '暂无反馈内容' });
        console.log(this.teachFeedback)
      } catch (e) {
        this.teachFeedback = [];
      } finally {
        this.teachFeedbackLoading = false; // 新增
      }
    },
    openSettings() {
      this.showSettingsModal = true;
    },
    closeSettings() {
      this.showSettingsModal = false;
    },
    saveSettings() {
      this.showSettingsModal = false;
    },
    resetAll() {
      this.messages = [
        {
          role: 'bot',
          text: '你好！我是你的Conflict Simulation助手。我可以帮助你模拟和分析各种冲突场景，并提供个性化的指导。'
        }
      ];
      this.input = '';
      this.scoreFeedback = null;
      this.teachFeedback = [];
      this.scoreFeedbackLoading = false;
      this.teachFeedbackLoading = false;
      this.conversation_id = ''; // 重置对话ID，开始新对话
      this.score_id = '';
      this.teach_id = '';
    }
  }
}
</script>

